<template>
	<view class="list">
		<view class="fixbg" :style="{ 'background-image' : 'url('+playlist.coverImgUrl+')' }"></view>
		<musichead title="歌单" :icon="true" color="white"></musichead>
		<view class="container" v-show="!isLoading">
			<scroll-view scroll-y="true">
				<view class="list-head">
					<view class="list-head-img">
						<image :src="playlist.coverImgUrl"></image>
						<text class="iconfont icon-yousanjiao">{{playlist.playCount | formatCount }}</text>
					</view>
					<view class="list-head-text">
						<view>{{playlist.name}}</view>
						<view>
							<image :src="playlist.creator.avatarUrl"></image>
							{{ playlist.creator.nickname }}
						</view>
						<view>{{playlist.description}}</view>
					</view>
				</view>
				<!-- #ifdef MP-WEIXIN -->
				<button class="list-share" open-type="share">
					<text class="iconfont icon-fenxiang"></text>分享给微信好友
				</button>
				<!-- #endif -->
				<view class="list-music">
					<view class="list-music-head">
						<text class="iconfont icon-bofang1"></text>
						<text>播放全部</text>
						<text>(共{{playlist.trackCount}}首)</text>
					</view>
					<!-- <view class="list-music-item">
						<view class="list-music-top">1</view>
						<view class="list-music-song">
							<view>与我无关</view>
							<view>
								<image src="../../static/dujia.png" mode=""></image>
								<image src="../../static/sq.png" mode=""></image>
								阿荣-与我无关
							</view>
						</view>
						<text class="iconfont icon-bofang"></text>
					</view> -->
					<view class="list-music-item" v-for="(item,index) in playlist.tracks" 
					@tap="handleToDetail(item.id)">
						<view class="list-music-top">{{ index +1}}</view>
						<view class="list-music-song">
							<view>{{item.name}}</view>
							<view>
								<image v-if="privileges[index].flag >60 &&
								 privileges[index].flag <70" src="../../static/dujia.png" mode=""></image>
								<image v-if="privileges[index].maxbr == 999000" src="../../static/sq.png" mode=""></image>
								{{item.ar[0].name}}-{{item.name}}
							</view>
						</view>
						<text class="iconfont icon-bofang"></text>
					</view>
				</view>
			</scroll-view>
		</view>
	</view>
</template>

<script>
	import '@/common/iconfont.css';
	import musichead from '../../components/musichead/musichead.vue'
	import { list } from '../../common/api.js'
	export default {
		data() {
			return {
				playlist :{
					creator :{
						avatarUrl :' '
					},
					trackCount:'',
				},
				privileges:[],
				isLoading: true,
			}
		},
		components:{
			musichead
		},
		onLoad(options) {
			//console.log(options.Id);
			
			uni.showLoading({
				title:'正在加载...',
			});
			list(options.Id).then((res)=>{
				if(res[1].data.code=='200'){
					this.playlist=res[1].data.playlist;
					this.privileges=res[1].data.privileges;
					this.$store.commit('INIT_TOPLISTIDS',res[1].data.playlist.trackIds);
					this.isLoading=false;
					uni.hideLoading();
				}
			});
		},
		methods: {
			handleToDetail(songId){
				uni.navigateTo({
					url:'../detail/detail?songId='+ songId
				});
			}
		}
	}
</script>

<style scoped>
	.list-head{ display: flex; margin:30rpx;}
		.list-head-img{ width:265rpx; height:265rpx; border-radius: 15rpx; margin-right:40rpx; overflow: hidden; position: relative;}
		.list-head-img image{ width:100%; height:100%;}
		.list-head-img text{ position: absolute; font-size:26rpx; color:white; right:8rpx; top:8rpx;}
		.list-head-text{ flex:1; font-size:24rpx; color:#c3d1e3;}
		.list-head-text image{ width:52rpx; height:52rpx; border-radius: 50%;}
		.list-head-text view:nth-child(1){ font-size:34rpx; color:#ffffff;}
		.list-head-text view:nth-child(2){ display: flex; align-items: center; margin:30rpx 0;}
		.list-head-text view:nth-child(2) text{ margin-left: 15rpx;}
		.list-head-text view:nth-child(3){ line-height: 38rpx;}
		
		.list-share{ width:330rpx; height:72rpx; margin:0 auto; background:rgba(0,0,0,0.4); text-align: center; line-height: 72rpx; font-size:26rpx; color:white; border-radius: 36rpx;}
		.list-share text{ margin-right:15rpx;}
		
		.list-music{background: white; border-radius: 50rpx;margin-top: 40rpx;overflow: hidden;}
		.list-music-head{height: 50rpx;margin: 30rpx 0 70rpx 22rpx;}
		.list-music-head text:nth-child(1){height: 50rpx;font-size: 50rpx;}
		.list-music-head text:nth-child(2){font-size:30rpx;margin: 0 10rpx 0 26rpx;}
		.list-music-head text:nth-child(3){font-size: 26rpx;color: #b2b2b2;}
		.list-music-item{display: flex;margin: 0 32rpx 66rpx 46rpx;align-items: center;color: #959595;}
		.list-music-top{width: 58rpx;font-size: 30rpx;line-height: 30rpx;}
		.list-music-song{flex: 1;}
		.list-music-song view:nth-child(1){font-size: 28rpx;color: black;width: 70vw;white-space: nowrap;
		overflow: hidden;text-overflow: ellipsis;}
		.list-music-song view:nth-child(2){font-size: 20rpx;align-items: center;width: 70vw;white-space: nowrap;
		overflow: hidden;text-overflow: ellipsis;}
		.list-music-song view:nth-child(2) image{width: 32rpx;height: 20rpx;margin-right: 10rpx;}
		.list-music-item text{font-size: 50rpx;color: #c7c7c7;}

</style>
